import React from "react";
import "../T&C/TC.css";

import axios from "axios";
import Conditions from "../T&C/conditions";

import { Link } from "react-router-dom";

export default class TC extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      agree: false,
      //sol_ID:
    };
    this.handleSol = this.handleSol.bind(this);
    this.handleSeek = this.handleSeek.bind(this);
  }

  async handleSol() {
    let name = localStorage.getItem("username");
    if (!this.state.agree) {
      await axios
        .post("/contract/agree/" + name + "/" + this.props.contractNumber + "/")
        .then((response) => {
          if (response.status == 200) {
            this.setState({ agree: true });
            document.getElementById("solbutton").disabled = "true";
          }
        });
    }
  }
  async handleSeek() {
    //let contractNumber = this.state.contractNumber;
    let ID = this.props.solnID.solId + ",";
    axios
      .post("/contract/seekerAgree/" + this.props.contractNumber + "/")
      .then((response) => {
        if (response.status == 200) {
          axios
            .post(
              "/sol/solverParticipationRp/" +
                this.props.contractNumber +
                "/" +
                ID +
                "/"
            )
            .then((response) => {});
        }
      });
  }
  render() {
    return (
      <div>
        <br />
        <br />
        <div>
        
        </div>
        <br />
        <br />
        <div
          style={{
            border: "2px solid black",
            marginLeft: "3rem",
            marginRight: "3rem",
            padding: "2rem 1rem",
          }}
        >
        <div 
        style={{
          backgroundColor:"#323754",
          color:"white"
          }}>
          <h4 
            style={{textAlign:"center"}}>
            Terms and Conditions
          </h4>
        </div>
        
          <div style={{ fontSize: "17px" }}>
            <Conditions />
            <br />
            <br />
          </div>
          <div style={{ textAlign: "center" }}>
            {!this.props.check ? (
              <center>
                <h6 style={{ color: "green", marginBottom: "3%" }}>
                  You have successfully completed this contract
                </h6>
              </center>
            ) : this.props.solverView ? (
              <div>
                <button
                  style={{
                    marginRight: "30rem",
                    backgroundColor: "green",
                    color: "#ffffff",
                  }}
                  type="button"
                  id="solbutton"
                  class="btn"
                  onClick={this.handleSol}
                >
                  SOLVER AGREE
                </button>

                <button type="button" class="btn btn-secondary" disabled="true">
                  SEEKER AGREE
                </button>
              </div>
            ) : (
              <div>
                {this.props.agree ? (
                  <div>
                    <h6 style={{ color: "green", marginRight: "40rem" }}>
                      Solver Agreed..
                    </h6>
                    <Link to="/dashboard/">
                      <button
                        style={{
                          float: "right",
                          marginRight: "20%",
                          marginTop: "-1.5%",
                          backgroundColor: "green",
                          color: "#ffffff",
                        }}
                        type="button"
                        class="btn"
                        onClick={this.handleSeek}
                      >
                        SEEKER AGREE
                      </button>
                    </Link>
                  </div>
                ) : (
                  <div style={{ float: "right", marginRight: "20%" }}>
                    <button
                      style={{
                        marginRight: "30rem",
                        backgroundColor: "green",
                        color: "#ffffff",
                      }}
                      type="button"
                      class="btn btn-secondary"
                    >
                      SOLVER AGREE
                    </button>
                    <button
                      type="button"
                      style={{ backgroundColor: "#aba9a9", color: "#ffffff" }}
                      class="btn"
                      disabled="true"
                    >
                      SEEKER AGREE
                    </button>
                    <br />
                    <h6 style={{ color: "red", marginLeft: "80%" }}>
                      Wait Until Solver Agree..
                    </h6>
                  </div>
                )}
              </div>
            )}

            <br />
            <br />
            <br />
          </div>
          {/* <div style={{textAlign:"center"}}> 
                <button type="button" class="btn btn-success">SUBMIT</button>
            </div> */}
        </div>
        <br />
        <br />
        <div style={{ marginRight: "2rem", textAlign: "right" }}>
          <button type="button" class="btn btn-secondary">
            PRINT AGREEMENT
          </button>
        </div>
        <div></div>
      </div>
    );
  }
}
