import React, { Component } from "react";

import axios from "axios";
import { Link } from "react-router-dom";
import "../ProblemInvolved/style.css";

export default class Table extends Component {
  constructor(props) {
    super(props);

    this.state = {
      expired: [],
      isLoading: false,
    };
  }

  async componentDidMount() {
    axios
      .get("/dashboard/expired/" + localStorage.getItem("username") + "/")
      .then((response) => {
        this.setState(
          (this.state.expired = this.state.expired.concat(response.data))
        );
        this.setState({ isLoading: true });
        console.log(this.state.expired);
        console.log(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    const expired = this.state.expired;
    return (
      <div className="tab main_content">
        {/* <div
          className="card"
          style={{
            textAlign: "centre",
            paddingLeft: "25rem",
            marginBottom: "1rem",
            backgroundColor: "midnightblue",
          }}
        >
          <h3 style={{ textAlign: "centre", paddingTop: "4px", color: "snow" }}>
            Expired Problems
          </h3>
        </div> */}
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginBottom: "1rem",
            backgroundColor: "midnightblue",
          }}
        >
          <h3 style={{ paddingTop: "4px", color: "snow" }}>Expired Problems</h3>
        </div>

        <table className="table table-bordered table-dark">
          <thead>
            <tr>
              <th>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Problems&nbsp;&nbsp;&nbsp;&nbsp;
              </th>
              <th>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                Days&nbsp;&nbsp;&nbsp;&nbsp;
              </th>
            </tr>
          </thead>
          <tbody>
            {expired.map((item) => (
              <tr key={item.problemId}>
                <td>
                  <center>
                    <Link
                      to={{ pathname: "/prblmdesc", state: { query: item } }}
                    >
                      {item.title}
                    </Link>
                  </center>
                </td>
                <td>
                  <center>
                    <Link
                      to={{ pathname: "/prblmdesc", state: { query: item } }}
                    >
                      {item.RnD_Budget}
                    </Link>
                  </center>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}
