import React from "react";
import Navbar from "../Navbar/nav";
import "../ProblemInvolved/style.css";

import { Modal } from "react-bootstrap";

import axios from "axios";
import { registerPlugin } from "react-filepond";
import "filepond/dist/filepond.min.css";
import "filepond-plugin-image-preview/dist/filepond-plugin-image-preview.css";
import FilePondPluginImagePreview from "filepond-plugin-image-preview";
import FilePondPluginFileValidateType from "filepond-plugin-file-validate-type";
import SolutionUpdate from "./solutionupdate";
registerPlugin(FilePondPluginImagePreview);
registerPlugin(FilePondPluginFileValidateType);

class solutionedit extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,

      solution: [],
      days: "",
      reason: "",
      docs: "",
      username: localStorage.getItem("username"),
      problemId: Number(this.props.location.state.query),
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleModal = this.handleModal.bind(this);
    this.onChangeFile = this.onChangeFile.bind(this);
  }

  handleClose() {
    this.setState({ show: !this.state.show });
  }
  handleModal() {
    this.setState({ show: !this.state.show });
  }
  onSubmit() {
    let formdata = new FormData();
    if (
      this.state.reason !== "" &&
      this.state.days !== "" &&
      this.state.docs !== ""
    ) {
      formdata.append("problemId", this.state.problemId);
      formdata.append("username", this.state.username);
      formdata.append("reason", this.state.reason);
      formdata.append("days", this.state.days);
      formdata.append("docs", this.state.docs);

      axios.post("/dashboard/extendDaysView/", formdata).then((res) => {
        if (res.status == 200) {
          document.getElementById("send").disabled = true;
          this.setState({
            reason: "",
            days: "",
            docs: "",
          });
        }
      });
      document.getElementById("error").innerHTML = "";
    } else {
      document.getElementById("error").innerHTML = "Fill all the Details";
    }
  }

  handleChange(e) {
    let target = e.target;
    let value = target.value;
    let name = target.name;

    this.setState({
      [name]: value,
    });
    // if(document.getElementById(''))
    document.getElementById("error").innerHTML = "";
  }
  onChangeFile = (e) => {
    let file = e.target.files[0];
    this.setState({ docs: file });
    document.getElementById("file").innerHTML = file.name;
    document.getElementById("error").innerHTML = "";
  };

  componentDidMount() {
    const pid = this.props.location.state.query;
    axios
      .get(
        "/dashboard/mySolutionEdit/" +
          localStorage.getItem("username") +
          "/" +
          pid +
          "/"
      )
      .then((res) => {
        this.setState({ solution: res.data });
        // this.setState({problemId:pid})
      });
  }

  render() {
    return (
      <div>
        <Navbar />

        <div className="main_content">
          <h3 className="ml-3">
            MySolutions
            <button
              type="submit"
              className="btn btn-primary float-right"
              onClick={() => this.handleModal()}
            >
              Extend Deadline
            </button>
            <br></br>
            <Modal show={this.state.show}>
              <Modal.Header className="float">
                <Modal.Title className="mx-auto">
                  Days to be Extended
                </Modal.Title>
                <button
                  type="button"
                  class="close"
                  aria-label="Close"
                  onClick={() => {
                    this.handleClose();
                  }}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </Modal.Header>
              <Modal.Body>
                <div className="form-group">
                  <div>
                    {" "}
                    No. Of Days
                    <input
                      type="number"
                      className="form-control"
                      id="days"
                      required
                      name="days"
                      onChange={this.handleChange}
                    />
                  </div>
                  <br></br>
                  <div>
                    {" "}
                    Reason
                    <textarea
                      type="text"
                      className="form-control"
                      placeholder="Describe your reason here..."
                      rows={5}
                      id="reason"
                      required
                      name="reason"
                      onChange={this.handleChange}
                    />
                    <br />
                  </div>
                </div>
                <div class="custom-file">
                  <input
                    type="file"
                    class="custom-file-input"
                    onChange={this.onChangeFile}
                  />
                  <label class="custom-file-label" id="file">
                    Select File
                  </label>
                </div>
              </Modal.Body>

              <Modal.Footer>
                <div
                  id="error"
                  className="mx-auto"
                  style={{ color: "red" }}
                ></div>
                <button
                  className="btn btn-primary"
                  id="send"
                  onClick={() => {
                    this.onSubmit();
                  }}
                >
                  {" "}
                  Send Request
                </button>
              </Modal.Footer>
            </Modal>
          </h3>
          {this.state.solution.map((solution, index) => (
            <SolutionUpdate solution={solution} index={index} />
          ))}
        </div>
      </div>
    );
  }
}
export default solutionedit;
