const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  layout: {
    width: "auto",
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: "auto",
      marginRight: "auto",
    },
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: "flex",
    justifyContent: "flex-end",
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

const steps = ["Personal Information", "Professional Details"];

function getStepContent(step, handleNext, handleBack) {
  switch (step) {
    case 0:
      return <AddressForm handleNext={handleNext} activeStep={step} />;
    case 1:
      return <PaymentForm activeStep={step} handleBack={handleBack} />;
    default:
      throw new Error("Unknown step");
  }
}

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <Link color="inherit" href="https://material-ui.com/">
        VignaTree
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

function UpdateTest() {
  const classes = useStyles();
  // const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);

  const initialValues2 = {
    qualification: "",
    specialization: "",
    displaydomains: [],
    domains: [],
    work_exp: "",
  };
  //   return (
  //     <div style={{ display: "flex", justifyContent: "center" }}>
  //       <main style={{ marginTop: "10vh" }}>
  //         <Paper elevation={6} style={{ padding: "10%" }}>

  //         </Paper>
  //       </main>
  //     </div>
  //   );
  // }

  // export default UpdateTest;

  // export default function Checkout() {

  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  return (
    <React.Fragment>
      <CssBaseline />

      <main className={classes.layout}>
        <Paper className={classes.paper}>
          <Typography component="h1" variant="h4" align="center">
            Update Your Profile
          </Typography>
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep !== steps.length ? (
              <React.Fragment>
                {getStepContent(activeStep, handleNext, handleBack)}
              </React.Fragment>
            ) : null}
          </React.Fragment>
          <React.Fragment>
            <div className={classes.buttons}>
              {activeStep !== 0 && (
                <Button onClick={handleBack} className={classes.button}>
                  Back
                </Button>
              )}
              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
              >
                {activeStep === steps.length - 1 ? "Place order" : "Next"}
              </Button>
            </div>
          </React.Fragment>
        </Paper>
        <Copyright />
      </main>
    </React.Fragment>
  );
}
export default UpdateTest;
